import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { setLoggedInUser } from "../../store/actions/userActions";

export const AppFooter = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser);
  }, [loggedUser, dispatch]);

  const onGoContacts = () => {
    history.push("/contact");
  };
  return (
    <div className="app-footer">
      {loggedUser && history.location.pathname != "/contact" && (
        <button className="transfer-btn" onClick={onGoContacts}>
          Contacts
        </button>
      )}
    </div>
  );
};
